@media (max-width:768px){
  .center_div{
    width: 55%;
  }
  .todo_style .fa-times{
    margin: 0px 15px 0px 15px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  button{
    margin-left: 5px;
    min-height: 40px;
    width: 40px;
    color: #fff;
    font-size: 30px;
  }
}